import React, { useState } from "react";
import Avatar from "@mui/material/Avatar";
import avatar from "../../recources/avatar.png";
import { useFormik } from "formik";
import AddPhotoAlternateIcon from "@mui/icons-material/AddPhotoAlternate";
import * as Yup from "yup";
import AddLinkIcon from "@mui/icons-material/AddLink";
import TagFacesIcon from "@mui/icons-material/TagFaces";
import Button from "@mui/material/Button";
import AddCircleOutlineIcon from '@mui/icons-material/AddCircleOutline';
import RequestCard from "./RequestCard";


// ::::::::::::::::::::::::::::::::::::::::::::::::::::::::::

const validationSchema = Yup.object().shape({
  content: Yup.string().required("you can't make an empty request"),
});

const HomeSection = () => {
  const [uploadingImage, SetUploadingImage] = useState(false);
  const [selectImage, setSelectedImage] = useState("");
  const handleSubmit = (values) => {
    console.log("values", values);
  };
const formik = useFormik({
    initialValues: {
      content: "",
      image: "",
    },
    onSubmit: handleSubmit,
    validationSchema,
  });
const handleSelectImage = (event) => {
    SetUploadingImage(true);
    const imgUrl = event.target.files[0];
    formik.setFieldValue("image", imgUrl);
    setSelectedImage(imgUrl);
    SetUploadingImage(false);
};
// ::::::::::::::::::::::::::::::::::::::::::::::::::::::::::::::::::::::
  return (
    <div className="space-y-5">
      <section>
        <h1 className="py-5 text-xl font-bold opacity-90">Home</h1>
      </section>
      <section className={"pb-10"}>
        <div className={"flex space-x-5"}>
          <Avatar alt="username" src={avatar} />
          <div className="w-full">
            <form onSubmit={formik.handleSubmit}>
              <div>
                <textarea
                  type="text"
                  name="content"
                  rows="3"
                  cols="10" 
                  wrap="soft"
                  placeholder="Make a wish"
                  className={"border-none outline-none text-xl bg-transparent w-full resize-none"}
                  {...formik.getFieldProps("content")}
                />
                {formik.errors.content && formik.touched.content && (
                  <span className={"text-red-500"}>
                    {formik.errors.content}
                  </span>
                )}
                <div className="flex justify-between items-center mt-5">
                  <div className="flex space-x-5 items-center">
                    <label className="flex items-center space-x-2 rounded-md cursor-pointer">
                      <AddPhotoAlternateIcon className="text-[#7c3aed]" />
                      <input
                        type="file"
                        name="image file"
                        className="hidden"
                        onChange={handleSelectImage}
                      ></input>
                    </label>
                    <AddLinkIcon className="text-[#7c3aed]" />
                    
                  </div>
                  <div>
                    <Button
                      sx={{
                        width: "100%",
                        borderRadius: "30px",
                        py: "5px",
                        bgcolor: "#7c3aed",
                        '&:hover': {
                          bgcolor: "#7c3Aed" // Maintain the same background color on hover
                      }
                      }}
                      variant="contained"
                      endIcon={<AddCircleOutlineIcon />}
                      type="submit"
                    >
                      Request
                    </Button>
                  </div>
                </div>
              </div>
            </form>
          </div>
        </div>
      </section>
      <section>

        {[1,1,1,1,1].map((item) => <RequestCard/> )}
            
      </section>
    </div>
  );
};

export default HomeSection;
